<template>
  <div id="chatchamp-wrapper">
    <ChatBubbleButton v-show="!isChatOpen" @click="handleChatToggle(true)">
      <!--img alt="Chatchamp Icon" src="./assets/chatchamp.svg" /-->
      <svg version="1.0" xmlns="http://www.w3.org/2000/svg" width="246.667" height="277.333" viewBox="0 0 185 208" svg-inline="" class="icon" role="presentation" focusable="false" tabindex="-1"><path d="M94.6 1.1c-3.7.5-10.2 2.1-14.5 3.5C24.6 23.5-.4 85 24.9 140.5c3.1 6.6 3.6 8.6 2.9 11.5-.4 1.9-1.9 9.1-3.4 16-2.3 11-3.5 17.1-5.9 30.8-.5 2.7-.3 3.2.7 2.6 2.1-1.4 4-1.3 5.4 0 1 1.1 2.3 1 7.1-.6 3.2-1 6.7-2.3 7.8-2.7 3.2-1.5 24.6-8.9 29.2-10.2 3.7-1.1 5.1-.9 13 1.7 8.5 2.8 9.5 2.9 28.3 2.9 20.8 0 23.1-.4 39-6.7 8.5-3.5 23-12.4 29.8-18.5 8.8-7.8 8.7-8.1-5.5-23.3-6.5-6.9-12.1-12.7-12.6-12.9-.5-.2-2.3 1-4 2.6-7.5 6.9-19.4 13.4-29.8 16.3-22.6 6.2-44.3-2.4-56.4-22.5-6-10-8-18.1-7.9-32 0-9.7.5-13.2 2.3-18.5 5.2-14.9 13.9-25.2 27-31.7 7-3.5 8.3-3.8 17.6-4.1 10.8-.4 19.5 1.2 26.9 5 6 3 19.2 12.5 21.5 15.3 1 1.4 2.3 2.5 2.7 2.4.5 0 6.2-6.6 12.7-14.6 13.7-16.9 14.1-18.1 7.5-23.8C157.7 5.7 126.9-3 94.6 1.1z"/></svg>
    </ChatBubbleButton>
    <ChatPanel v-show="isChatOpen" @close="handleChatToggle(false)"></ChatPanel>
  </div>
</template>

<script>
import ChatPanel from './components/ChatPanel.vue';
import ChatBubbleButton from './components/ChatBubbleButton.vue';
import { EventBus, SUBMIT_MESSAGE_ADMIN } from './util/global-event-bus';

export default {
  name: 'app',
  components: {
    ChatBubbleButton,
    ChatPanel,
  },
  data() {
    return {
      isChatOpen: false,
    };
  },
  mounted() {
    EventBus.$on(SUBMIT_MESSAGE_ADMIN, this.openChat);
  },
  beforeDestroy() {
    EventBus.$off(SUBMIT_MESSAGE_ADMIN, this.openChat);
  },
  methods: {
    openChat() {
      if (!this.isChatOpen) {
        this.handleChatToggle(true);
      }
    },
    handleChatToggle(isOpen) {
      this.isChatOpen = isOpen;
    },
  },
};
</script>

